import EXIF from '../../lib//exif.js'

export default {
    install: function (Vue) {
        Vue.file = Vue.prototype.$file = {
            upload: function (option) {
                option = {
                    type: option.type || 'image',
                    max: option.max || 5,
                    size: option.size || 200 * 1024,
                    success: option.success || function () {},
                    fail: option.fail || function () {}
                };

                let input = document.createElement("input");
                input.setAttribute('type', 'file');
                input.setAttribute('multiple', 'multiple');
                input.className = 'upload-image-20170608';
                input.style.display = 'none';

                if (option.type == 'image') {
                    input.setAttribute('accept', 'image/*');
                }

                document.body.appendChild(input);

                input.onchange = function () {

                    if (!this.files.length) {
                        return;
                    }

                    var files = Array.prototype.slice.call(this.files);

                    if (files.length > option.max) {
                        option.fail({msg: '' + option.max + ''});
                        return;
                    }

                    files.forEach(function (file) {

                        if (!/\/(?:jpeg|png|gif)/i.test(file.type)) {
                            return;
                        }

                        var reader = new FileReader();

                        reader.onload = function() {
                            var result = this.result;
                            var img = new Image();
                            img.src = result;
                            img.onload = function () {
                                drawPhoto(img, 0, 0, img.width, img.height, (src) => {
                                    var img = new Image();
                                    img.src = src;

                                    //100kb
                                    if (src.length <= option.size) {
                                        img = null;
                                        option.success(src, file.type);
                                    } else {

                                        if (img.complete) {
                                            callback();
                                        } else {
                                            img.onload = callback;
                                        }

                                        function callback() {
                                            option.success(compress(img), file.type);
                                            img = null;
                                        }

                                    }
                                });
                            };
                        };

                        reader.readAsDataURL(file);
                    });

                    var nodes = document.querySelectorAll('.upload-image-20170608');
                    for (var i = 0; i < nodes.length; i++) {
                        document.body.removeChild(nodes[i]);
                    }

                };

                input.click();
            }
        };

        // canvas
        function compress(img) {
            var canvas = document.createElement("canvas"),
                ctx = canvas.getContext('2d'),
                tCanvas = document.createElement("canvas"), // canvas
                tctx = tCanvas.getContext("2d"),
                width = img.width,
                height = img.height,
                ratio; // 400

            if ((ratio = width * height / 4000000) > 1) {
                ratio = Math.sqrt(ratio);
                width /= ratio;
                height /= ratio;
            } else {
                ratio = 1;
            }

            canvas.width = width;
            canvas.height = height;
            // 
            ctx.fillStyle = "#fff";
            ctx.fillRect(0, 0, canvas.width, canvas.height);
            // 100
            var count;

            if ((count = width * height / 1000000) > 1) {
                count = ~~(Math.sqrt(count) + 1); //
                // 
                var nw = ~~(width / count);
                var nh = ~~(height / count);
                tCanvas.width = nw;
                tCanvas.height = nh;
                for (var i = 0; i < count; i++) {
                    for (var j = 0; j < count; j++) {
                        tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
                        ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
                    }
                }
            } else {
                ctx.drawImage(img, 0, 0, width, height);
            }

            //
            var ndata = canvas.toDataURL('image/jpeg', 0.1);
            tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;

            return ndata;
        }

        //
        function getPhotoOrientation(img){
            var orient;
            EXIF.getData(img, function () {
                orient = EXIF.getTag(this, "Orientation");
            });
            return orient;
        }

        function drawPhoto (photo, x, y, w, h, callback) {

            //
            var orient = getPhotoOrientation(photo);
            var canvas = document.createElement("canvas");
            canvas.width = w;
            canvas.height = h;

            if (canvas.getContext) {
                var ctx = canvas.getContext("2d");
                //draw on Canvas
                var img = new Image();
                img.onload = function () {
                    var canvas_w = Number(ctx.canvas.width);
                    var canvas_h = Number(ctx.canvas.height);
                    //90
                    if (orient == 6) {
                        ctx.save();//
                        ctx.translate(canvas_w / 2, canvas_h / 2);//(0,0)
                        ctx.rotate(90 * Math.PI / 180);//90
                        // CanvasdrawImage
                        ctx.drawImage(img, Number(y) - canvas_h / 2, Number(x) - canvas_w / 2, h, w);//translate
                        ctx.restore();//
                    } else {
                        // CanvasdrawImage
                        ctx.drawImage(img, x, y, w, h);
                    }

                    callback(canvas.toDataURL());
                };

                img.src = photo.src; // 
            }
        }
    }
};




