<template>
  <div id="cancelActivity" class="container bc-page">
    <p class="ag-text"></p>
    <p class="p15-12 f16 bc-fff"></p>
    <div class="pr bt1-eee bb1-eee p20 f14 bc-fff">
      <textarea class="w b-none c-999" type="text" placeholder="" v-model="content" style="resize: none; min-height: 3.7333333333333334rem"></textarea>
      <span class="pa" style="bottom: 0.2666666rem; right: 0.2666666rem;">{{content.length}}/200</span>
    </div>
    <a class="ag-btn" style="margin-top: 0.53333333333rem;" @click="cancelActivity"></a>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    name: 'cancelActivity',
    created: function () {

    },
    data: function () {
      return {
        content: ''
      }
    },
    computed: {

    },
    methods: {
      cancelActivity: function () {
        this.$dialog.confirm({
          content: '',
          onOk: () => {
            this.$loading.show('...');
            this.$http.post('/user/activity/cancel', {data: {
              token: this.$store.state.user.info.token,
              activityNo: this.$route.query.activityNo,
              content: this.content
            }}).then((data) => {
              this.$loading.hide();
              if (data.code == 0) {
                this.$toast.info('');
                this.$router.back();
              } else {
                this.$toast.info(data.msg);
              }
            }, () => {
              this.$toast.info('');
              this.$loading.hide();
            });
          }
        });
      }
    },
    watch: {
      'content': function (val) {
        if (val.length > 200) {
          this.content = this.content.substr(0, 200);
        }
      }
    }
  }
</script>
<style scoped>

</style>



